import React, { useEffect, useState } from 'react'

function GetUserInfo() {
  const [users, setUsers] = useState([])
  const [query, setQuery] = useState('')
  const getData = ()=>{
    fetch('http://api.github.com/users/'+query)
    .then(response => response.json())
    .then(data =>{
      console.log(data);
       setUsers(data)
      
    })
  }
  // useEffect(()=>{
  //   getData()
  // },[])
  return (
    <>
      <h1>Get Data From API</h1>
      <input placeholder='Search here for deta' onChange={(e)=> setQuery(e.target.value)} />
      <button onClick={()=> getData()}>Get Data</button>
      <div>
        {
          users.map((myData)=>{
             return(
              <div key={myData.id}>
                <h1>User Name : {myData.login}</h1>
                <img src={myData.avatar_url}/>
                </div>
            )
          })
        }
      </div>
    </>
  )
}

export default GetUserInfo

