import React, { useState } from 'react'

function HOCComponent(WrapperComponent) {
  function HOCComponent(props){
    const [count, setCount] = useState(0)
    const increment = ()=>{
      setCount(count+1)
    }
    return(
      <WrapperComponent count={count} increment={increment} {...props}/>
    )
  }
  return HOCComponent
}
export default HOCComponent
