import React from 'react'
import HOCComponent from './HOCComponent'

function CompnentsB(props) {
  const {count, increment, name, color} = props
  return (
    <div style={{backgroundColor : color}}>
      <h1>CompnentsB</h1>
      <h2>Value : {count}</h2>
      <button onMouseOver={()=>increment()}>{name}</button>
    </div>
  )
}

export default HOCComponent(CompnentsB)
