import React, { useReducer } from 'react'
import reducer from './reducer'

const intialState = 1;
function Mutiply() {
    const [state, dispatch] = useReducer(reducer,intialState)
  return (
    <>
      <h1>{state}</h1>
      <button onClick={()=>dispatch({type : "MULTIPLY"})}>Mutiply BY 10</button>
    </>
  )
}

export default Mutiply
