import React from 'react'
import { Button } from '@mui/material'
import DeleteIcon from '@mui/icons-material/Delete';



function NewBtn() {
  return (
    <div style={{padding : '10px'}}>
      <h1>Button Section</h1>
   <Button>First</Button>
   <Button variant='contained' color='secondary'>Second</Button>
   <Button variant='outlined'>Third</Button>
   <Button endIcon={<DeleteIcon/>}>Delete</Button>
   <Button startIcon={<DeleteIcon/>}>Clean</Button>


    </div>
  )
}

export default NewBtn
