import { getValue } from '@testing-library/user-event/dist/utils'
import React, { useState } from 'react'
function EvenOddCounter() {                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                   
    const [counter, setCounter] = useState(1)
    const [inputVal, setInputVal] = useState(null)
    const getValue = (e)=>{
      console.log(e.target.value);
      setInputVal(e.target.value)
    }
    const increFun =()=>{
      setCounter(counter+1)
    }
    const evenNum = <h1>Even Number</h1>
    const oddNum = <h1>Odd Number</h1>
  return (
    <>
    <h1>Even Odd Application</h1>
    <input type='text' onChange={(e)=>getValue(e)} />
    <h2>Counter Value : {counter}</h2>
    <button onClick={increFun}>Increment</button>
    <h1>User Name : {inputVal}</h1>
    </>
  )
}
export default EvenOddCounter
