

const reducer = (state, action)=>{
    console.log(state);
    console.log(action.type);
    if(action.type === "INCREMENT"){
        state = state+1
    }
    if(action.type === "DECREMENT"){
        state = state-1
    }
    if(action.type === "MULTIPLY"){
        state = state*10
    }
    return state
}

export default reducer
