import React, { useState } from 'react'

function UseStates() {
    const [counter, setCounter] = useState(0)
    const IncrFun = ()=>{
        setCounter(counter+1)
    }
    const DecrFun = ()=>{
        if(counter >0){
            setCounter(counter-1)
        }
        
    }
    const reset = ()=>{
        setCounter(0)
    }
  return (
    <>
      <h1>Use useState Method</h1>
      <h1>{counter}</h1>
      <button onClick={(IncrFun)}>IncrFun</button>
      <button onClick={(DecrFun)}>DecrFun</button>
      <button onClick={(reset)}>reset</button>


    </>
  )
}

export default UseStates
