import React from 'react'
import Bio from '../Components/Bio'
import Img from '../Components/Img'
import myProfile from '../Components/Images/my clone.jpg'

function About() {
  return (
    <div className='div'>
      <h1>About Section</h1>
      <Img name = 'Anuu' imgSrc = {myProfile} />
      <Bio bio = 'This is my About Content'/>
    </div>
    
  )
}

export default About
