import React from 'react'
import MyData from './Data'

function Card() {
  return (
    <>
<div className='cardDiv'>
      <h1>My Cards</h1>
      <div>      
            {
                MyData.map((data)=>{
                    return(
                        <div>
                        <h1>{data.Title}</h1>
                        <p>{data.Discription}</p>
                    </div> 
                    )
                    
                }
                    
                )  //Array.map
            }
         
      </div>
</div>
    </>
  )
}

export default Card
