import React from 'react'
import HOCComponent from './HOCComponent'

function ComponentsA(props) {
  const {count, increment, name, color} = props
  return (
    <div style={{backgroundColor : color, color : 'blue'}}>
      <h1>ComponentsA</h1>
      <h2>Value : {count}</h2>
      <button onClick={()=>increment()}>{name}</button>
    </div>
  )
}

export default HOCComponent(ComponentsA)
